* {
  box-sizing: border-box;
}

body {
  height: 100vh;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  justify-content: center;
  align-items: center;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
  display: flex;
  position: relative;
}

a {
  position: absolute;
  top: 10px;
  left: 10px;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  max-width: 100%;
  display: block;
}

button {
  cursor: pointer;
  margin: 5px;
  padding: 10px 20px;
  font-size: 16px;
}

button[data-start] {
  color: #fff;
  background-color: #4caf50;
}

button[data-stop] {
  color: #fff;
  background-color: #f44336;
}

